/* Fonts */
@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Light.ttf");
  font-weight: lighter;
}

@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Roboto;
  src: url("/css/fonts/Roboto-Bold.ttf");
  font-weight: bold;
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Light.ttf");
  font-weight: lighter;
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Regular.ttf");
}

@font-face {
  font-family: RobotoCondensed;
  src: url("/css/fonts/RobotoCondensed-Bold.ttf");
  font-weight: bold;
}

@font-face {
  font-family: FiraCode;
  src: url("/fonts/FiraSans-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: FiraCode;
  src: url("/fonts/FiraSans-SemiBold.ttf");
  font-weight: bolder;
  font-style: normal;
}

@font-face {
  font-family: FiraCode;
  src: url("/fonts/FiraSans-Bold.ttf");
  font-weight: bold;
  font-style: normal;
}

/* Media Queries */
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
}

body {
  font-family: "RobotoCondensed";
  font-size: 16px;
  line-height: 1.5em;
}

a {
  text-decoration: none;
  color: inherit;
}

@media only screen and (max-width: 767px) {
  nav {
    background-color: #ce181f;
    display: grid;
    grid-template-columns: 5fr auto auto;
    height: 48px;
    position: fixed;
    top: 0;
    width: 100%;
  }
  nav img {
    -ms-flex-item-align: center;
    align-self: center;
    height: 36px;
    margin-left: 4px;
  }
}

@media only screen and (max-width: 767px) and (min-width: 980px) {
  nav img {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  nav .active {
    background-color: #e6e7e8 !important;
  }
  nav .active i,
  nav .active p {
    color: #2F2F2F !important;
  }
  nav .mobile-button-container {
    background-color: #2F2F2F;
    cursor: pointer;
    display: grid;
    padding: 6px 0;
  }
  nav .mobile-button-container .mobile-button-item {
    color: #fff;
    display: grid;
    justify-items: center;
    grid-gap: 10px;
    padding-left: 10px;
  }
  nav .mobile-button-container .mobile-button-item p {
    font-size: 12px;
    line-height: 0;
    padding-bottom: 5px;
    text-transform: uppercase;
    margin: 0;
  }
  nav .mobile-button-container .mobile-button-item i {
    font-size: 24px;
    padding-top: 2px;
  }
  nav .mobile-navigation {
    background-color: #e6e7e8;
    position: fixed;
    right: 0;
    top: 48px;
    height: calc(100% - 47px);
    margin-bottom: 150px;
    overflow-y: auto;
    -webkit-transition-duration: 1s;
    transition-duration: 1s;
    width: 0;
    z-index: 1000;
  }
  nav .container {
    border-bottom: 3px solid #8C8C8C;
    display: grid;
    grid-template-columns: 1fr;
    padding: 0 10px;
  }
  nav .container a {
    color: #4c4c4c;
    letter-spacing: 1px;
    line-height: 18px;
    padding: 10px;
    text-transform: uppercase;
  }
  nav .container a:not(:nth-child(7)) {
    border-bottom: 1px solid #B6B6B6;
  }
  nav .first,
  nav .second {
    display: grid;
    grid-template-columns: 1fr;
    padding: 0 10px;
  }
  nav .first a,
  nav .second a {
    color: #4c4c4c;
    line-height: 18px;
    padding: 10px;
  }
  nav .first a:not(:nth-child(7)),
  nav .second a:not(:nth-child(7)) {
    border-bottom: 1px solid #B6B6B6;
  }
  nav .search-navigation {
    background-color: #e6e7e8;
    position: fixed;
    right: 0;
    top: 48px;
    height: calc(100% - 47px);
    margin-bottom: 150px;
    overflow-y: auto;
    -webkit-transition-duration: 1s;
    transition-duration: 1s;
    width: 0;
    z-index: 1000;
  }
  nav .search-navigation .search-box {
    border-bottom: 3px solid #8C8C8C;
    display: grid;
    grid-template-columns: 1fr;
    padding: 0 10px;
    text-transform: uppercase;
  }
  nav .search-navigation .search-box a {
    color: #4c4c4c;
    letter-spacing: 1px;
    line-height: 18px;
    padding: 10px;
  }
  nav .search-navigation .search-items {
    display: grid;
    grid-template-columns: 1fr;
    padding: 0 10px;
  }
  nav .search-navigation .search-items a {
    color: #4c4c4c;
    line-height: 18px;
    padding: 10px;
  }
  nav .search-navigation .search-items a:not(:nth-child(3)) {
    border-bottom: 1px solid #B6B6B6;
  }
}

@media only screen and (min-width: 768px) {
  .main-navigation {
    background-color: #ce181f;
  }
  .main-navigation .container {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(7, 1fr);
    min-height: 70px;
    margin: auto;
    width: 95%;
    max-width: 1170px;
  }
  .main-navigation .container a {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #fff;
    font-size: 1.1em;
    letter-spacing: 1.5px;
    line-height: 1.1em;
    margin: 0 5px;
    text-align: center;
    text-transform: uppercase;
  }
  .main-navigation .container .nav-condense {
    font-size: .95em;
    letter-spacing: -1px;
  }
}

@media only screen and (min-width: 768px) and (min-width: 980px) {
  .main-navigation .container .nav-condense {
    letter-spacing: inherit;
    font-size: 1.1em;
  }
}

@media only screen and (min-width: 768px) and (min-width: 1200px) {
  .main-navigation .container .container {
    width: 80%;
  }
}

@media only screen and (min-width: 768px) {
  .sub-navigation {
    background-color: #e6e7e8;
  }
  .sub-navigation .sub-nav-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: auto;
    width: 95%;
    max-width: 1170px;
  }
  .sub-navigation a {
    color: #333;
    font-size: .9em;
    line-height: 2rem;
    letter-spacing: .4px;
    padding-right: 6px;
    margin-right: 4px;
  }
  .sub-navigation a:hover {
    color: #595959;
  }
  .sub-navigation .first a:not(:nth-child(3)) {
    border-right: 1px solid #333;
  }
  .sub-navigation .second {
    justify-self: end;
  }
  .sub-navigation .second a:not(:nth-child(5)) {
    border-right: 1px solid #333;
  }
}

footer {
  background-color: #2F2F2F;
  color: #fff;
  padding: 20px 0 50px;
  font-family: "Roboto", sans-serif;
  font-size: .9em;
  line-height: 1.7em;
  letter-spacing: .4px;
}

footer a:hover {
  text-decoration: underline;
}

footer .footer-container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}

@media only screen and (min-width: 768px) {
  footer .footer-container {
    grid-template-columns: 1fr 4fr;
    justify-items: start;
    margin: auto;
    width: 95%;
    max-width: 1170px;
  }
}

footer .footer-container .footer-brand {
  display: grid;
}

footer .footer-container .footer-brand img {
  margin-bottom: 25px;
  justify-self: center;
}

@media only screen and (min-width: 768px) {
  footer .footer-container .footer-brand img {
    margin-bottom: 0;
    grid-row: 1 / 4;
  }
}

footer .footer-container .footer-brand .social-media {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(5, minmax(50px, 1fr));
}

footer .footer-container .footer-brand .social-media span {
  font-size: 1.5em;
}

@media only screen and (min-width: 768px) {
  footer .footer-container .footer-brand .social-media {
    grid-template-columns: repeat(5, minmax(40px, 1fr));
    grid-row: 4 / 6;
  }
}

@media only screen and (min-width: 980px) {
  footer .footer-container .footer-brand .social-media {
    grid-row: 5 / 7;
  }
}

@media only screen and (min-width: 768px) {
  footer .footer-container .footer-brand {
    grid-template-rows: repeat(8, 1fr);
  }
}

footer .footer-container .campus-links {
  display: grid;
  grid-gap: 25px;
  justify-items: center;
  margin-bottom: 25px;
  margin-top: 25px;
}

@media only screen and (min-width: 768px) {
  footer .footer-container .campus-links {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    grid-auto-flow: column;
    grid-gap: 0;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: repeat(4, auto);
    justify-items: start;
    margin-top: 0;
    margin-left: 15px;
    width: 100%;
  }
  footer .footer-container .campus-links a {
    padding: 0 5px 1em;
  }
}

footer .suu-brand-bar {
  background-color: #ce181f;
  font-size: 1.3em;
  font-family: "FiraCode", sans-serif;
  font-weight: bolder;
  letter-spacing: .1em;
  padding: .5em;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}

@media only screen and (min-width: 768px) {
  footer .suu-brand-bar {
    font-size: 1.5em;
    grid-column: 1 / -1;
  }
}

footer .suu-details address {
  font-style: normal;
  line-height: 18px;
  padding: .7em 0;
  margin-bottom: 18px;
  text-align: center;
  text-transform: uppercase;
}

@media only screen and (min-width: 768px) {
  footer .suu-details {
    grid-column: 1 / -1;
  }
}

.border-right {
  border-right: 1px solid #777777;
  padding-right: 10px;
}

.padding-right {
  padding-right: 5px;
}

@media only screen and (max-width: 767px) {
  .desktop-only {
    display: none;
  }
}

@media only screen and (min-width: 768px) {
  .mobile-only {
    display: none;
  }
}

/* Delete Me */
.spacer {
  height: 500px;
}
